'use client';

import React from 'react';
import { CategoryData } from '../types/category';

interface CategoryRankingProps {
  data: CategoryData;
}

const CategoryRanking: React.FC<CategoryRankingProps> = ({ data }) => {
  const { title, color, currentRank, totalRank, description, scores } = data;

  // ÎûúÎç§ÏúºÎ°ú 2Í∞úÏùò Ìï≠Î™©ÏùÑ ÏÑ†ÌÉùÌïòÏó¨ ÌÖåÎßàÏÉâ Ïú†ÏßÄ
  const themeColorIndices = React.useMemo(() => {
    const indices = Array.from({ length: scores.length }, (_, i) => i);
    for (let i = indices.length - 1; i > 0; i--) {
      const j = Math.floor(Math.random() * (i + 1));
      [indices[i], indices[j]] = [indices[j], indices[i]];
    }
    return indices.slice(0, 1);
  }, [scores.length]);

  return (
    <div
      className="flex w-full flex-col rounded-lg bg-white p-6 shadow-sm"
      style={{ marginBottom: '5rem' }}
    >
      <div className="relative mb-4">
        {/* ÌÉÄÏù¥ÌãÄ */}
        <div
          style={{
            fontSize: '2rem',
            fontWeight: 800,
            color,
            marginBottom: '20px',
          }}
        >
          {title}
        </div>
      </div>

      {/* ÏÑ§Î™Ö */}
      <div
        className="mb-6 whitespace-pre-line text-gray-600"
        style={{ lineHeight: '1.5rem', marginBottom: '1rem' }}
      >
        {description}
      </div>

      {/* ÏÑ∏Î∂Ä Ï†êÏàò Í∑∏Î¶¨Îìú */}
      <div className="grid grid-cols-4" style={{ gap: '1.2rem' }}>
        {scores.map((score, index) => {
          // ÎûúÎç§ÏúºÎ°ú ÏÑ†ÌÉùÎêú Ìï≠Î™©Îßå ÌÖåÎßàÏÉâ Ïú†ÏßÄ, ÎÇòÎ®∏ÏßÄÎäî #E7E8EA
          const backgroundColor = themeColorIndices.includes(index)
            ? color
            : '#E7E8EA';
          const textColor = themeColorIndices.includes(index)
            ? 'white'
            : '#000000';

          return (
            <div
              key={index}
              className="flex flex-col rounded-lg p-6"
              style={{
                backgroundColor,
                padding: '1.2rem',
                borderRadius: '10px',
                aspectRatio: '1',
                justifyContent: 'space-between',
              }}
            >
              <span
                className="mb-3 text-sm"
                style={{
                  fontSize: '1.1rem',
                  fontWeight: 600,
                  color: textColor,
                }}
              >
                {score.indicator}
              </span>
              <span
                className="mb-2 text-2xl font-extrabold"
                style={{
                  fontSize: '2rem',
                  fontWeight: 600,
                  color: textColor,
                }}
              >
                {score.score}
              </span>
              <span
                className="mb-2 text-xl font-bold"
                style={{
                  fontWeight: 600,
                  color: textColor,
                }}
              >
                {score.rank}ÏúÑ
              </span>
              <span
                className="text-sm"
                style={{
                  fontSize: '1rem',
                  color: themeColorIndices.includes(index)
                    ? 'rgba(255, 255, 255, 0.8)'
                    : '#666666',
                }}
              >
                {score.source}
              </span>
            </div>
          );
        })}
      </div>
    </div>
  );
};

export default CategoryRanking;
